.project {
  position: relative;
  margin: 1rem 5rem;
  display: grid;
  grid-template-columns: 1fr 6fr;
}
.index {
  font-size: 8rem;
  color: var(--secondary-font-color);
  position: relative;
}

.details {
  padding-left: 1rem;
  display: flex;
  flex-direction: column;
  color: var(--primary-font-color);
}

.details .heading {
  margin-top: 1rem;
  font-size: 1.5rem;
}

.details .description {
  position: relative;
  text-align: justify;
  margin-top: 0.5rem;
  font-size: 1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  line-clamp: 2;
  -webkit-box-orient: vertical;
}

@media only screen and (max-width: 600px) {
  .project {
    margin: 1rem 2rem 1rem 1rem;
  }
  .index {
    font-size: 6rem;
  }
}

