.Intro {
  height: max-content;
  position: relative;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 2rem;
}
.introSection2 {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.introSection1 {
  position: relative;
  height: min-content;
  display: flex;
  height: 100%;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  padding: 20%;
}

.introSection1 p {
  letter-spacing: 1px;
  text-align: justify;
  margin-bottom: 3rem;
  font-weight: 500;
  color: var(--primary-font-color);
}
.introSection1 a {
  margin: 1rem 0;
  align-self: flex-start;
  background-color: var(--primary-color);
  color: #eeeeee;
  text-decoration: none;
  padding: 0.6rem 1.2rem 0.4rem 1.2rem;
  border-radius: 4px;
}
@media only screen and (max-width: 1100px) {
  .introSection1 {
    padding: 15%;
  }
}
@media only screen and (max-width: 1000px) {
  .Intro {
    grid-template-columns: none;
  }
  .introSection2 {
    grid-row: 1;
  }
  .introSection1 {
    padding: 10%;
    grid-row: 2;
    padding: 1rem 2rem;
  }
  .introSection1 p {
    font-size: 18px;
  }
  .introSection1 a {
    align-self: flex-end;
    padding: 0.6rem 1.2rem 0.4rem 1.2rem;
  }
}
