.footer{
    background-color: #444444;
    color: #dddddd;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 1rem 0.5rem 1rem;
}

.footer .links{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
}
.footer span{
    font-family: "motiva italic";
    display: flex;
    align-items: center;
}

.footer span svg{
    font-size: 22px;
    color: rgb(230, 40, 40);
    margin: 0 0.3rem;
}

.footer a{
    color: #dddddd;
}
.footer a svg{
    font-size: 26px;
}


@media only screen and (max-width: 600px){
    .footer{
        flex-direction: column-reverse;
        padding: 1rem;
        gap: 1rem;
    }
    .footer span svg{
        font-size: 20px;
        margin: 0 0.3rem;
    }
   
}