:root {
  --primary-color: #784cfb;
  --primary-bg-color: #eeeeee;
  --primary-font-color: #222222;
  --track-width: 5px;
  --secondary-font-color: #bbbbbb;
}

* {
  margin: 0;
  padding: 0;
  font-family: "motiva";
  box-sizing: border-box;
}

@font-face {
  font-family: "motiva";
  src: url("./assets/fonts/motiva/MotivaSansTest-Medium.otf");
}

@font-face {
  font-family: "motiva italic";
  src: url("./assets/fonts/motiva/MotivaSansTest-MediumItalic.otf");
}

::-webkit-scrollbar {
  width: 0.4rem;
}
::-webkit-scrollbar-thumb {
  background: var(--primary-color);
  border-radius: 8px;
}

::-webkit-scrollbar-corner {
  background: #000;
}

body {
  overflow-x: hidden;
  background: var(--primary-bg-color);
  color: var(--primary-font-color);
}

@media (prefers-color-scheme: dark) {
  :root {
    --primary-color: #784cfb;
    --primary-bg-color: #333333;
    --primary-font-color: #eeeeee;
    --secondary-font-color: #555555;
  }
}
