.custom-orbits {
  position: relative;
  aspect-ratio: 1/1;
  animation: none;
  --track-width: 2px;
}
.custom-orbits::before,
.custom-orbits::after {
  position: absolute;
  content: "";
  aspect-ratio: 1/1;
  border: var(--track-width) solid var(--primary-color);
  border-radius: 50%;
}
.custom-orbits::before {
  height: 13%;
  right: -10%;
  top: -10%;
}
.custom-orbits::after {
  height: 26%;
  bottom: -20%;
  left: -20%;
}
.custom-outer,
.custom-middle,
.custom-inner {
  aspect-ratio: 1/1;
  border-radius: 50%;
}
.custom-outer {
  height: 100%;
}
.custom-middle {
  height: 78%;
}
.custom-inner {
  height: 65%;
}
.custom-outer,
.custom-middle,
.custom-inner {
  position: relative;
  border: var(--track-width) solid var(--primary-color);
  border-radius: 50%;
  aspect-ratio: 1/1;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: none;
}

.custom-outer::after,
.custom-middle::after,
.custom-inner::after,
.custom-outer::before,
.custom-middle::before,
.custom-inner::before {
  content: "";
  height: 0.4rem;
  aspect-ratio: 1/1;
  background-color: blue;
  background-color: var(--primary-bg-color);
  position: absolute;
}

.custom-inner::before {
  left: 0;
  transform: translateX(-50%);
}
.custom-inner::after {
  right: 0;
  transform: translateX(50%);
}
.custom-middle::before {
  top: 0;
  right: 0;
  rotate: 45deg;
}
.custom-middle::after {
  bottom: 0;
  left: 0;
  rotate: -45deg;
}

.custom-outer::before {
  top: 0;
  left: 0;
  rotate: 45deg;
}
.custom-outer::after {
  bottom: 0;
  right: 0;
  rotate: 45deg;
  transform: translate(-50%);
}
.orbits {
  position: relative;
  height: 17rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  margin: 7rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: revolution 10s linear infinite;
}

.orbits::before,
.orbits::after {
  position: absolute;
  content: "";
  aspect-ratio: 1/1;
  border: var(--track-width) solid var(--primary-color);
  border-radius: 50%;
}
.orbits::before {
  height: 13%;
  right: -8%;
  top: -8%;
}
.orbits::after {
  height: 26%;
  bottom: -18%;
  left: -18%;
}
.outer,
.middle,
.inner {
  position: relative;
  border: var(--track-width) solid var(--primary-color);
  border-radius: 50%;
  aspect-ratio: 1/1;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: none;
  animation: revolution 10s infinite linear;
}

.outer::before,
.middle::before,
.inner::before,
.outer::after,
.middle::after,
.inner::after {
  content: "";
  height: 1rem;
  background-color: var(--primary-bg-color);
  aspect-ratio: 1/1;
  position: absolute;
}
.outer::before,
.middle::before,
.inner::before {
  top: -0.5rem;
  left: unset;
  right: unset;
}
.outer::after,
.middle::after,
.inner::after {
  bottom: -0.5rem;
  right: unset;
  left: unset;
}
.outer {
  height: 100%;
}
.middle {
  height: 78%;
}
.inner {
  height: 70%;
}

.animation-container {
  margin: unset;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.animation-container img {
  user-select: none;
  top: unset;
  clip-path: ellipse(47% 50% at 50% 50%);
  height: 20rem;
  position: absolute;
  bottom: 23.6%;
  rotate: -1deg;
  z-index: 1;
}
@media only screen and (max-width: 600px) {
  .orbits {
    height: 13rem;
    border-radius: 50%;
  }

  .animation-container img {
    clip-path: ellipse(47.6% 50% at 50% 50%);
    height: 15rem;
    bottom: 27%;
  }
}

@media only screen and (max-width: 400px) {
  .orbits {
    height: 12rem;
    border-radius: 50%;
  }

  .animation-container img {
    clip-path: ellipse(44.5% 50% at 50.2% 50.5%);
    height: 15rem;
    bottom: 28.3%;
  }
}

@media only screen and (max-width: 360px) {
  .orbits {
    height: 11rem;
    border-radius: 50%;
  }

  .animation-container img {
    height: 14rem;
    clip-path: ellipse(44% 50% at 50.1% 49%);
    bottom: 28.5%;
  }
}
@media only screen and (max-width: 320px) {
  .orbits {
    height: 9.6rem;
    border-radius: 50%;
  }

  .animation-container img {
    height: 13rem;
    clip-path: ellipse(42% 50% at 50.5% 50%);
    bottom: 31%;
  }
}
@media only screen and (max-width: 290px) {
  .orbits {
    height: 8.8rem;
    border-radius: 50%;
  }

  .animation-container img {
    height: 11rem;
    clip-path: ellipse(44.3% 50% at 50.2% 50.5%);
    bottom: 32.3%;
  }
}
@keyframes revolution {
  from {
    rotate: 360deg;
  }
}
