.projects {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 4rem;
}

@media only screen and (max-width: 600px) {
  .projects {
    grid-template-columns: 1fr;
    margin-bottom: 2rem;
  }
}

@media only screen and (max-width: 1150px) {
  .projects {
    grid-template-columns: 1fr;
    margin-bottom: 2rem;
  }
}
