.sidd {
  top: 0;
  left: 0;
  position: absolute;
  font-size: 5rem;
  writing-mode: vertical-rl;
  rotate: 180deg;
  line-height: 5rem;
  color: var(--secondary-font-color);
}
@media only screen and (max-width: 600px) {
}

@media only screen and (max-width: 400px) {
}

@media only screen and (max-width: 360px) {
  .sidd {
    line-height: 4rem;
    font-size: 4rem;
  }
}
@media only screen and (max-width: 320px) {
  .sidd {
    line-height: 3.5rem;
    font-size: 3.5rem;
  }
}
@media only screen and (max-width: 290px) {
  .sidd {
    font-size: 3rem;
    line-height: 3rem;
  }
}
@keyframes mover {
  0% {
    scale: 0;
  }
  5% {
    scale: 1;
  }
  95% {
    top: var(--toppp);
    right: var(--righttt);
    bottom: var(--bottommm);
    left: var(--lefttt);
    rotate: var(--rotationAngle);
    scale: 1;
    opacity: 0.8;
  }
  100% {
    top: var(--toppp);
    right: var(--righttt);
    bottom: var(--bottommm);
    left: var(--lefttt);
    rotate: var(--rotationAngle);
    opacity: 0;
    scale: 0;
  }
}
